.main {
    .swiper-wrapper {
        max-width: 100%;
    }
    .slide-list {
        width: 100%;
        overflow: hidden;
    }
    .banner {
        margin-bottom: 80px;
        a {
            display: block;
            margin-top: 15px;
        }
    }
    .visual-slide {
        margin-top: 26px;
        height: 585px;
        overflow: hidden;
        .swiper-wrapper {
            .no-center-banner {
                .left-banner,
                .right-banner {
                    width: 592px;
                    height: 585px;
                }

                .right-banner {
                    margin-left: 16px;
                }
            }

            .left-banner,
            .center-banner,
            .right-banner {
                float: left;
                position: relative;
                overflow: hidden;
                img {
                    width: 100%;
                }
            }

            .center-banner,
            .right-banner {
                margin-left: 15px;
            }

            .left-banner,
            .right-banner {
                width: 450px;
                height: 585px;
            }

            .center-banner {
                width: 270px;
            }
        }
        .swiper-controls {
            position: relative;
            width: 1200px;
            margin: 0 auto;
            .swiper-button-prev {
                margin-left: -1px;
                margin-right: -1px;
            }
            .swiper-button-prev,
            .swiper-button-next {
                margin-top: -315px;
            }
        }
    }
    .thumb-wrap {
        margin-top: 80px;
    }
    .brand {
        .brand-list {
            text-align: center;
            li {
                display: inline-block;
                a {
                    display: block;
                    margin: 0 17px 17px;
                    padding: 3px 0;
                    font-size: 11px;
                    color: #1f1f1f;
                    font-weight: bold;
                    border-bottom: 3px solid transparent;
                    &.active {
                        border-bottom: 3px solid #0f0f10;
                    }
                }
            }
        }
        .more-wrap {
            margin-top: 40px;
            margin-bottom: 10px;
            text-align: right;
            a {
                display: inline-block;
                padding: 3px 15px;
                font-weight: bold;
                font-size: 11px;
                color: #0f0f10;
                border: 2px solid #0f0f10;
            }
        }
    }
}
.main-cont.special-cont .category-menu {
    margin-top: 20px;
}
